  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  font-size: 62.5%;
}

body {
  font-size: 1.6rem;
  color: #333;
  font-family: "Mulish", sans-serif;
}

h1, h2, h3, h4 {
  margin-bottom: 2rem;
}

h1 {
  font-size: 3.5rem;
}

h2 {
  font-size: 3rem;
}

h3 {
  font-size: 2.5rem;
}

h4 {
  font-size: 2rem;
}

ul {
  list-style: none;
}

img {
  max-width: 100%;
}

a {
  color: #333;
  text-decoration: none;
}

.container {
  min-height: 100vh;
  display: grid;
  grid: "header" auto "content" 1fr "footer" auto/auto;
}

.content {
  background-color: #ecf0f1;
  grid-area: content;
  padding: 20px;
}

.title-underline {
  padding-bottom: 2rem;
  border-bottom: 1px solid #ecf0f1;
}

.btn {
  border: none;
  border-radius: 5px;
  padding: 1rem 2rem;
  font-weight: 700;
  cursor: pointer;
}
.btn:hover {
  box-shadow: 0 1px 2px 0 rgba(60, 64, 67, 0.3), 0 1px 3px 1px rgba(60, 64, 67, 0.15);
  transition: box-shadow 0.3s;
}
.btn-primary {
  background-color: rgb(46, 170, 204);
  color: white;
}
.btn-secondary {
  border: 1px solid rgb(46, 170, 204);
  background: white;
  color: rgb(46, 170, 204);
}
.btn-danger {
  background-color: rgb(224, 75, 5);
  color: white;
}

.text-error {
  color: rgb(224, 75, 5);
}

.active {
  color: rgb(46, 170, 204);
  font-weight: bold;
}

.calc {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.calc .modal {
  width: 30%;
  background: white;
  border: 1px solid #333;
  border-radius: 5px;
  padding: 40px;
  box-shadow: 0 1px 2px 0 rgba(60, 64, 67, 0.3), 0 1px 3px 1px rgba(60, 64, 67, 0.15);
  text-align: center;
}
.calc .modal p {
  font-size: 2rem;
}
.calc .modal .btn {
  margin-top: 30px;
  margin-right: 10px;
}

header {
  grid-area: header;
  padding: 20px;
  background-color: #276fae;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
header a {
  color: white;
}
header .header-brand {
  font-size: 4rem;
  font-weight: 700;
}
header ul {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
header ul li .header-nav {
  font-size: 1.8rem;
  padding: 0 10px;
}

footer {
  grid-area: footer;
  background: #333;
  padding: 20px;
  color: white;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
